import React, {Component} from 'react'
import logo from '../img/logo.png';
import {Link} from 'react-scroll';

class Navbar extends Component {
    state = {};

    showNav() {
        var nav = document.getElementById('nav-ul');
        nav.style.display = 'block';
    }

    close() {
        var nav = document.getElementById('nav-ul');
        nav.style.display = 'none';
    }

    render() {
        return (
            <nav className='nav'> 
                <div className='logo'>
                    <img src={logo} alt="Logo" />
                    <h1>Maestro Pizinni</h1>
                </div>
                <div className="nav-list">
                    <div className="mobile-nav">
                        <a className='menu-icon' onClick={this.showNav}><i class="fas fa-bars"></i></a>
                    </div>
                    <ul id='nav-ul'>
                        <li><Link to='home' smooth={true} duration={1000}>Home</Link></li>
                        <li><Link to='about' smooth={true} duration={1000}>About</Link></li>
                        <li><Link to='menu' smooth={true} duration={1000}>Menu</Link></li>
                        <li><Link to='contact' smooth={true} duration={1000}>Contact</Link></li>
                        <a className='close-btn' onClick={this.close}>X<i class="fas fa-close"></i></a>
                    </ul>
                </div>
            </nav>
        )
    }
}

export default Navbar;