import React from 'react';
function MenuCards(props) {

    // var card = document.getElementsByClassName('.card')
    

    return (
        <div className='card'>
            <img src={props.img} alt="Pizza-img" />
            <h3>{props.name}</h3>
            <p>{props.text}</p>
            <h4>{props.price}</h4>
            <div className="orderNow">
                <a href="">Order Now</a>
            </div>
        </div>
    )
}

export default MenuCards;
