import React from 'react';
import AboutImg from '../img/pizza.jpg';
function About() {
    return (
        <div className='container' id='about'>
            <div className="about">
                <div className="about-img">
                    <img src={AboutImg} alt="About Image" />
                </div>
                <div className="about-text">
                    <h3>About Us</h3>
                    <h1>WELCOME TO MAESTRO PIZZINI</h1>
                    <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Pariatur est, itaque facilis modi tempora, 
                        quo voluptatibus sapiente nam nulla cupiditate atque adipisci explicabo voluptatum delectus sunt eius officiis sed. 
                        Dolor autem dolorem necessitatibus et illo quibusdam excepturi officiis obcaecati, 
                        odio labore adipisci laborum commodi!</p>
                </div>
            </div>
        </div>
    )
}

export default About;
